/**
 * @module hooks/useMetaDataApi
 * @author Charles Blais, Natural Resource Canada <charles.blais@canada.ca>
 */
import { useState, useEffect, useReducer } from 'react';
import axios from 'axios';

/**
 * Reducer for hook state
 * @param {object} state 
 * @param {objcet} action 
 */
const dataFetchReducer = (state, action) => {
  switch (action.type) {
    case 'FETCH_INIT':
      return {
        ...state,
        isLoading: true,
        isError: false
      };
    case 'FETCH_SUCCESS':
      return {
        ...state,
        isLoading: false,
        isError: false,
        data: action.payload,
      };
    case 'FETCH_FAILURE':
      return {
        ...state,
        isLoading: false,
        isError: true,
      };
    default:
      throw new Error();
  }
};


/**
 * Hook
 * ====
 * 
 * Download metadata for the provided URL.  JSON object expected.
 * 
 * @param {string} initialUrl 
 * @param {object} initialData 
 */
const useMetaDataApi = (initialUrl, initialData) => {
  const [url, setUrl] = useState(initialUrl);

  const [state, dispatch] = useReducer(dataFetchReducer, {
    isLoading: false,
    isError: false,
    data: initialData,
  });

  useEffect(() => {
    let didCancel = false;

    const fetchData = async () => {
      dispatch({ type: 'FETCH_INIT' });

      try {
        const result = await axios(url);

        if (!didCancel) {
          dispatch({ type: 'FETCH_SUCCESS', payload: result.data });
        }
      } catch (error) {
        if (!didCancel) {
          dispatch({ type: 'FETCH_FAILURE' });
        }
      }
    };

    fetchData();

    return () => {
      didCancel = true;
    };
  }, [url]);

  return [state, setUrl];
};



export default useMetaDataApi;